export default {
	props: {
		recentItem: Object,
		clickType: ""
	},
	methods: {
		haneleItemClick() {
			if (this.clickType == "publish") {
				uni.$emit("backPublish", this.recentItem)
				uni.navigateBack({
					delta: 1
				})
			} else {
				uni.navigateTo({
					url: "/pages/news/hot-sort-detail/hot-sort-detail?topicItem=" + JSON.stringify(this.recentItem)
				})
			}
		}
	}
}
